import data from "../data/products.json"
import { Link } from "react-router-dom"
import { useEffect, useState } from "react";

function ProductsList(){
    const [productlist, setProductlist] = useState([]);
    const getProductList = () => {
        setProductlist(data);
    }
    useEffect(()=>{
        getProductList()
    }, []);
    return (
        <section className="page-section bg-light" id="products">
            <div className="container">
                <div className="text-center">
                    <h2 className="section-heading text-uppercase">products</h2>
                    <div className="row">
                        {/* <select style={{marginBottom: "1rem"}}>
                            <option></option>
                            <option></option>
                            <option>/</option>
                            <option>/</option>
                            <option></option>
                        </select> */}
                        {productlist.map((product) => (
                            <div className="col-lg-3 col-sm-6 mb-4" key={product.id}>
                                <div className="product-item" id="product">
                                    <Link
                                        className="product-link"
                                        to={process.env.PUBLIC_URL + `/product-detail/${product.id}`}
                                    >
                                        <div className="product-hover">
                                            <div className="product-hover-content">
                                                <i className="fas fa-plus fa-3x"></i>
                                            </div>
                                        </div>
                                        <div className={product.rental ? "" : ""}>{product.rental ? "" : ""}</div>
                                        <img
                                            className="img-fluid"
                                            src={product.imgSrc[0]}
                                            alt={product.id.slice(0,4)}
                                            loading="lazy"
                                        />
                                    </Link>
                                    <Link
                                        className="product-link"
                                        to={process.env.PUBLIC_URL + `/product-detail/${product.id}`}
                                    >
                                        <div className="product-caption">
                                            <div className="product-caption-heading">No.{product.num}</div>
                                            <h5>{product.name}</h5>
                                            <div className="product-caption-subheading text-muted">
                                                {product.type}/{product.size}
                                            </div>
                                        </div>  
                                    </Link>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            </div>
        </section>
    )
}

export default ProductsList;