import { useEffect, useState } from 'react';
import Carousel from 'react-bootstrap/Carousel';
import { Link } from 'react-router-dom';
import Price from './Price.js'

function Productinfo({product}){
    const [index, setIndex] = useState(0);
    const handleSelect = (selectedIndex, e) => {
        setIndex(selectedIndex);
    };
    useEffect(() => {
        handleSelect();
    }, []);
    return (
        <div
            className="product-modal"
            role="dialog"
        >
            <div className="modal-dialog">
                <div className="modal-content">
                    <div className="container">
                        <div className="row justify-content-center">
                            <div className="col-lg-5">
                                <h3>No.{product.num} <br/>{product.name}</h3>
                                <div className={product.rental ? "" : ""}>{product.rental ? "" : ""}</div>
                                    <Carousel activeIndex={index} onSelect={handleSelect} indicators={true}>
                                        { product.imgSrc ? product.imgSrc.map((img) => (
                                            <Carousel.Item key={img}>
                                                <img
                                                    className="d-block w-100"
                                                    src={img}
                                                    alt={img}
                                                />
                                            </Carousel.Item>
                                        ))
                                            : null
                                        }
                                    </Carousel>
                                    <table border="solid">
                                        <tbody>
                                            <tr>
                                                <th></th>
                                                <td>{product.num}</td>
                                            </tr>
                                            <tr>
                                                <th></th>
                                                <td>{product.name}</td>
                                            </tr>
                                            <tr>
                                                <th></th>
                                                <td>{product.type}/{product.size}</td>
                                            </tr>
                                            <tr>
                                                <th></th>
                                                <td>{product.zip ? "" : ""}</td>
                                            </tr>
                                            <tr>
                                                <th></th>
                                                <td>{product.extension ? "" : ""}</td>
                                            </tr>
                                            <tr>
                                                <th></th>
                                                <td>{product.wheels}</td>
                                            </tr>
                                            <tr>
                                                <th></th>
                                                <td>{product.sidehandle ? "" : ""}</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                    {product.type === "" ?
                                        <p>*      .(+2,000)</p>
                                        :
                                        (product.rental ?
                                            <Link
                                                to={process.env.PUBLIC_URL + `/product-order/${product.id}`}
                                            >
                                                <button
                                                    className="btn btn-primary btn-xl text-uppercase"
                                                    type="button"
                                                >
                                                    
                                                </button>
                                            </Link>
                                            : <p>*   .</p>)
                                    }
                                <Price />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Productinfo;